/* eslint-disable max-len */

const uiBbTransactionDetailsComponent = {
  controller: 'uiBbTransactionDetailsController',
  bindings: {
    transaction: '<',
    showAccountNumber: '<',
    onChangeCategory: '&?',
    buttonLabels: '<',
    fieldLabels: '<',
  },
  template: `
    <div class="transaction-details">
      <div class="transaction-details-change-category-btn row" data-ng-if="$ctrl.transaction.category && $ctrl.onChangeCategory">
        <p class="col-12">
          <button class="btn btn-link p-0" data-ng-click="$ctrl.changeCategory()" data-ng-bind="$ctrl.buttonLabels.changeCategory"></button>
        </p>
      </div>
      <div class="transaction-details-account-number row" data-ng-if="$ctrl.showAccountNumber">
        <p class="transaction-details-label col-6 col-md-4 font-weight-bold" data-ng-bind="$ctrl.fieldLabels.accountNumber"></p>
        <p class="transaction-details-value col-6 col-md-8" data-ng-bind="$ctrl.transaction.accountNumber"></p>
      </div>
      <div class="transaction-details-type-group row" data-ng-if="$ctrl.transaction.typeGroup">
        <p class="transaction-details-label col-6 col-md-4 font-weight-bold" data-ng-bind="$ctrl.fieldLabels.transactionType"></p>
        <p class="transaction-details-value col-6 col-md-8" data-ng-bind="$ctrl.transaction.typeGroup"></p>
      </div>
      <div class="transaction-details-counter-party-name row" data-ng-if="$ctrl.transaction.counterPartyName">
        <p class="transaction-details-label col-6 col-md-4 font-weight-bold" data-ng-bind="$ctrl.fieldLabels.counterPartyName"></p>
        <p class="transaction-details-value col-6 col-md-8" data-ng-bind="$ctrl.transaction.counterPartyName"></p>
      </div>
      <div class="transaction-details-counter-party-account-number row" data-ng-if="$ctrl.transaction.counterPartyAccountNumber">
        <p class="transaction-details-label col-6 col-md-4 font-weight-bold" data-ng-bind="$ctrl.fieldLabels.counterPartyAccountNumber"></p>
        <p class="transaction-details-value col-6 col-md-8" data-ng-bind="$ctrl.transaction.counterPartyAccountNumber"></p>
      </div>
      <div class="transaction-details-scheduled-date row" data-ng-if="$ctrl.transaction.scheduledDate">
        <p class="transaction-details-label col-6 col-md-4 font-weight-bold" data-ng-bind="$ctrl.fieldLabels.scheduledDate"></p>
        <p class="transaction-details-value col-6 col-md-8" data-ng-bind="$ctrl.transaction.scheduledDate"></p>
      </div>
      <div class="transaction-details-reference row" data-ng-if="$ctrl.transaction.reference">
        <p class="transaction-details-label col-6 col-md-4 font-weight-bold" data-ng-bind="$ctrl.fieldLabels.transactionReference"></p>
        <p class="transaction-details-value col-6 col-md-8" data-ng-bind="$ctrl.transaction.reference"></p>
      </div>
      <div class="transaction-details-instructed-currency row" data-ng-if="$ctrl.transaction.instructedCurrency">
        <p class="transaction-details-label col-6 col-md-4 font-weight-bold" data-ng-bind="$ctrl.fieldLabels.instructedCurrency"></p>
        <p class="transaction-details-value col-6 col-md-8" data-ng-bind="$ctrl.transaction.instructedCurrency"></p>
      </div>
      <div class="transaction-details-exchange-rate row" data-ng-if="$ctrl.transaction.currencyExchangeRate">
        <div class="transaction-details-label col-6 col-md-4 font-weight-bold" data-ng-bind="$ctrl.fieldLabels.currencyExchangeRate"></div>
        <div class="transaction-details-value col-6 col-md-8" data-ng-bind="$ctrl.transaction.currencyExchangeRate"></div>
      </div>
      <div class="transaction-details-instructed-amount row" data-ng-if="$ctrl.transaction.instructedAmount">
        <p class="transaction-details-label col-6 col-md-4 font-weight-bold" data-ng-bind="$ctrl.fieldLabels.instructedAmount"></p>
        <p class="transaction-details-value col-6 col-md-8">
          <span class="debit-credit-sign" data-ng-bind="$ctrl.transaction.debitCreditSign"></span>
          <ui-bb-format-amount-ng
            class="amount-regular-color"
            data-amount="$ctrl.transaction.instructedAmount"
            data-currency="$ctrl.transaction.instructedCurrency">
          </ui-bb-format-amount-ng>
        </p>
      </div>
      <div class="transaction-details-description row" data-ng-if="$ctrl.transaction.description">
        <p class="transaction-details-label col-6 col-md-4 font-weight-bold" data-ng-bind="$ctrl.fieldLabels.description"></p>
        <p class="transaction-details-value col-6 col-md-8" data-ng-bind="$ctrl.transaction.description"></p>
      </div>
      <div class="transaction-details-category row" data-ng-if="$ctrl.transaction.category">
        <p class="transaction-details-label col-6 col-md-4 font-weight-bold" data-ng-bind="$ctrl.fieldLabels.category"></p>
        <p class="transaction-details-value col-6 col-md-8" data-ng-bind="$ctrl.transaction.category"></p>
      </div>
      <div class="transaction-details-check-serial-number row" data-ng-if="$ctrl.transaction.checkSerialNumber">
        <p class="transaction-details-label col-6 col-md-4 font-weight-bold" data-ng-bind="$ctrl.fieldLabels.checkSerialNumber"></p>
        <p class="transaction-details-value col-6 col-md-8" data-ng-bind="$ctrl.transaction.checkSerialNumber"></p>
      </div>
      <div class="transaction-details-running-balance row" data-ng-if="$ctrl.transaction.runningBalance">
        <p class="transaction-details-label col-6 col-md-4 font-weight-bold" data-ng-bind="$ctrl.fieldLabels.runningBalance"></p>
        <p class="transaction-details-value col-6 col-md-8">
          <ui-bb-format-amount-ng
            class="amount-regular-color"
            data-amount="$ctrl.transaction.runningBalance">
          </ui-bb-format-amount-ng>
        </p>
      </div>
    </div>
  `,
};

export default uiBbTransactionDetailsComponent;



